import React, { useState } from "react";
import SourcingDetailsTabBody from "./SourcingDetailsTabBody";

function SourcingDetailsTabs() {
  const [activeTab, setActiveTab] = useState("details");
  return (
    <div className="tabSection">
      <div className="tabHead">
        <button className={activeTab === "details" ? "active" : ""} onClick={e => setActiveTab("details")}>
          Details
        </button>
        <button className={activeTab === "refund" ? "active" : ""} onClick={e => setActiveTab("refund")}>
          {"Return & Refund Policy"}
        </button>
        <button className={activeTab === "ask" ? "active" : ""} onClick={e => setActiveTab("ask")}>
          Ask A Question
        </button>
        <button className={activeTab === "reviews" ? "active" : ""} onClick={e => setActiveTab("reviews")}>
          Reviews
        </button>
        <button className={activeTab === "facebook" ? "active" : ""} onClick={e => setActiveTab("facebook")}>
          Facebook AD
        </button>
      </div>
      <SourcingDetailsTabBody activeTab={activeTab} setActiveTab={setActiveTab} />
    </div>
  );
}

export default SourcingDetailsTabs;
