import React from "react";

function Switch(props) {
  return (
    <div className={`switch ${props.switchState ? "switchOn" : "initial"} `} onClick={() => props.setSwitchState(prev => !prev)}>
      <b className={`onSwitch ${props.showText ? "" : "dontShow"}`}>ON</b>
      {/* <b className="switchOff">OFF</b> */}
      <b className="switch_Circle"></b>
    </div>
  );
}

export default Switch;
