import React from "react";
import PageHead from "../Header/PageHead";
import ProductList from "./Products/ProductList";

import ProductImportProducts from "./Products/ProductImportProducts";
import SideMenuShow from "./SideMenu/SideMenuShow";
import SourceContentBody from "./Sourcing/SourceContentBody";

function PageContent(props) {
  // console.log(props.selectAll);

  return (
    <>
      {!props.sourcing && (
        <div className="page-content">
          <SideMenuShow toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} />
          <PageHead setModalName={props.setModalName} setShowModal={props.setShowModal} logState={props.logState} title={props.title} pathBase={props.pathBase} pathId={props.pathId} catagory={props.catagory} />
          <div className="page-content__body">
            {props.catagory === "main" && <ProductImportProducts toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} />}
            {props.catagory === "impProd" && <ProductList pathId={props.pathId} selectAll={props.selectAll} setSelectAll={props.setSelectAll} prodImpLi={props.prodImpLi} setProdImpLi={props.setProdImpLi} catagory={props.catagory} />}
            {props.catagory === "myProd" && <ProductList pathId={props.pathId} MyProdLi={props.MyProdLi} setMyProdLi={props.setMyProdLi} catagory={props.catagory} selectAll={props.selectAll} setSelectAll={props.setSelectAll} />}
            {props.catagory === "editProd" && <ProductList catagory={props.catagory} />}
          </div>
        </div>
      )}
      {props.sourcing && (
        <div className="page-content">
          <div className="page-content__body">
            <SideMenuShow toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} />
            {!props.details && <SourceContentBody sourceProductLi={props.sourceProductLi} />}
            {props.details && <SourceContentBody setModalName={props.setModalName} setShowModal={props.setShowModal} details={true} />}
          </div>
        </div>
      )}
    </>
  );
}

export default PageContent;
