import React from "react";
import ChangePass from "./ChangePass";
import CreateAccount from "./CreateAccount";
import ShippingMethod from "./ShippingMethod";

function ModalCenterDiv(props) {
  return (
    // <div className={`pass changePass modalCenterDiv`}>
    <div className={`modalCenterDiv ${props.className ? props.className : ""}`}>
      <div className="pass_head">
        <span>{props.title}</span>
        <div className="pass_cross" onClick={() => props.setShowModal(false)}>
          <div className="line l1"></div>
          <div className="line l2"></div>
        </div>
      </div>
      {props.modalName === "changePass" && <ChangePass />}
      {props.modalName === "createAccount" && <CreateAccount />}
      {props.modalName === "shippingMethod" && <ShippingMethod />}
    </div>
  );
}

export default ModalCenterDiv;
