import React from "react";

function SupplyDimensions(props) {
  return (
    <div className="supplier">
      <span>{props.title}:&nbsp;</span>
      <span className="supplier_actual">
        <img src={`${process.env.PUBLIC_URL}/images/${props.imgName}`} alt={props.imgAlt} />
        &nbsp; by {props.company}
      </span>
    </div>
  );
}

export default SupplyDimensions;
