import React from "react";

function SingleInfo(props) {
  const showChangePassClickHandler = () => {
    props.setShowModal(true);
    props.setModalName("changePass");
  };

  return (
    <tr className="account-page_singleInfo">
      <td className="title">{props.title}</td>
      {props.details && <td className="details">{props.details}</td>}
      {props.type === "pass" && (
        <td className="pass">
          ****** &nbsp;&nbsp;
          {/* setModalName={props.setModalName} setShowModal={props.setShowModal} */}
          <button onClick={showChangePassClickHandler}>Change Password</button>
        </td>
      )}
    </tr>
  );
}

export default SingleInfo;
