import React from "react";

function AccDet(props) {
  const openCreateAccountModalHandler = () => {
    props.setShowModal(true);
    props.setModalName("createAccount");
  };
  if (props.accPage) {
    return (
      <div className="main-page_head_accDet">
        {props.logState.loggedIn ? <div className="logo">B</div> : !props.logState.loggedIn ? <div className="logo notLogedIn">A</div> : ""}
        {props.logState.loggedIn ? (
          <div className="text">
            <h1>
              Beidou&nbsp;
              <span>{`(Premium)`}</span>
              <button>Account upgrade &gt;</button>
            </h1>
            <p>
              ID: &nbsp;<span>#4546871546</span>
            </p>
          </div>
        ) : !props.logState.loggedIn ? (
          <>
            <div className="text">
              <h1>
                App80&nbsp;
                <button>Account upgrade &gt;</button>
              </h1>
              <p>
                ID: &nbsp;<span>#4546871546</span>
              </p>
            </div>
            {/* setModalName={props.setModalName} setShowModal={props.setShowModal} */}
            <button onClick={openCreateAccountModalHandler} className="createAnAccount">
              Create an Account
            </button>
          </>
        ) : (
          ""
        )}
      </div>
    );
  } else {
    return (
      <div className="main-page_head_accDet">
        <div className="logo">B</div>
        <div className="text">
          <h1>
            Beidou&nbsp;
            <span>{`(Premium)`}</span>
            <button>Account upgrade &gt;</button>
          </h1>
          <p>
            ID: &nbsp;<span>#4546871546</span>
          </p>
        </div>
      </div>
    );
  }
}

export default AccDet;
