import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faStar } from "@fortawesome/free-solid-svg-icons";

function Rating(props) {
  return (
    <div className="ratings">
      <div className="stars">
        <FontAwesomeIcon className={`stars_icon`} icon={faStar} />
        <FontAwesomeIcon className={`stars_icon`} icon={faStar} />
        <FontAwesomeIcon className={`stars_icon`} icon={faStar} />
        <FontAwesomeIcon className={`stars_icon`} icon={faStar} />
        <FontAwesomeIcon className={`stars_icon unfilled`} icon={faStar} />
      </div>
      {props.headerRating && <p className="reviews">{`(${props.rateCount})`}</p>}
    </div>
  );
}

export default Rating;

Rating.defaultProps = {
  rateCount: 0
};
