import React from "react";
import DropBtn from "../UtilityComponents/DropBtn";

function SingleProdBtns(props) {
  if (props.btnCat === "impProd") {
    return (
      <div className="buttons">
        <DropBtn title="More Action" className="action" />
        <DropBtn title="Import to Store" />
      </div>
    );
  } else if (props.btnCat === "myProd") {
    return (
      <div className="buttons">
        <button className="simple">Link to Nextschain</button>
        <button className="simple redBtn">Remove</button>
      </div>
    );
  } else return <div></div>;
}

export default SingleProdBtns;
