import React from "react";

function SalesSingleRow(props) {
  const greatestNumber = () => {
    let largest = 0;
    for (let key in props.rowData) {
      if (props.rowData[key] > largest) {
        largest = props.rowData[key];
      }
    }
    return largest;
  };
  //   console.log(Math.max(...props.rowData));
  return (
    <tr>
      <td className="country">{props.name}</td>
      <td
        className={
          props.rowData.oneToThree === greatestNumber() ? "td_bold" : ""
        }
      >
        {props.rowData.oneToThree}%
      </td>
      <td
        className={
          props.rowData.fourToSeven === greatestNumber() ? "td_bold" : ""
        }
      >
        {props.rowData.fourToSeven}%
      </td>
      <td
        className={
          props.rowData.eightToNine === greatestNumber() ? "td_bold" : ""
        }
      >
        {props.rowData.eightToNine}%
      </td>
      <td
        className={
          props.rowData.tenToTwelve === greatestNumber() ? "td_bold" : ""
        }
      >
        {props.rowData.tenToTwelve}%
      </td>
      <td
        className={
          props.rowData.thirtToFift === greatestNumber() ? "td_bold" : ""
        }
      >
        {props.rowData.thirtToFift}%
      </td>
      <td
        className={
          props.rowData.sixtToTwen === greatestNumber() ? "td_bold" : ""
        }
      >
        {props.rowData.sixtToTwen}%
      </td>
      <td
        className={
          props.rowData.twentyPlus === greatestNumber() ? "td_bold" : ""
        }
      >
        {props.rowData.twentyPlus}%
      </td>
      <td>
        <button>Detail</button>
      </td>
    </tr>
  );
}

export default SalesSingleRow;
