import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExclamationTriangle } from "@fortawesome/free-solid-svg-icons";

function Alert(props) {
  const showCreateAccountModalHandler = () => {
    props.setShowModal(true);
    props.setModalName("createAccount");
  };
  return (
    <p className={`alert ${props.className ? props.className : ""} ${props.type === "warn" ? "warn" : ""}`}>
      {props.type === "warn" && <FontAwesomeIcon icon={faExclamationTriangle} className="alert-icon" />}
      {props.text}&nbsp;&nbsp;&nbsp;&nbsp;
      {props.className === "notLoggedInAlert" && <button onClick={showCreateAccountModalHandler}>{"Create an Account >"}</button>}
    </p>
  );
}

export default Alert;
