import React from "react";

function BussOppSingle(props) {
  return (
    <>
      {props.paraType === "simple" && <p>{props.text}</p>}
      {props.paraType === "list" && <p className="bussOps_li">{props.text}</p>}
      {props.paraType === "headSimp" && (
        <p className="bussOps_hs">
          <span>{props.head}</span>
          {props.text}
        </p>
      )}
    </>
  );
}

export default BussOppSingle;
