import React, { useState } from "react";
import country from "../../images/US 2.png";
import userImage from "../../images/user.png";
import notifSimp from "../../images/notif-simp.png";
import notifActiveEllipse from "../../images/notiif-active-ellipse.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch, faAngleDown } from "@fortawesome/free-solid-svg-icons";

function UserInfo(props) {
  const [hideSearch, setHideSearch] = useState(false);

  const toggleSearch = () => {
    setHideSearch(prev => !prev);
    if (!hideSearch) {
      document.querySelector(".search input").focus();
    } else {
      document.querySelector(".search input").blur();
    }
  };
  return (
    <div className="userInfo">
      <div onFocus={() => setHideSearch(true)} onBlur={() => setHideSearch(false)} className={`search ${hideSearch ? "" : "showSearch"}`}>
        <input
          // className={`${showSearch ? "showSearch" : ""}`}
          type="text"
          placeholder="Search for Buyer's name, order No., tracking no..."
        />
        <FontAwesomeIcon className={`search_icon`} icon={faSearch} onClick={toggleSearch} />
      </div>
      <div className="language">
        <img className="country" src={country} alt="country" />
        <span>English</span>
        <FontAwesomeIcon icon={faAngleDown} />
      </div>

      {props.main && (
        <div className="notif">
          <img src={`${process.env.PUBLIC_URL}/images/headerMessenger.png`} alt="notification" />
          <img src={notifActiveEllipse} alt="active notification" />
        </div>
      )}
      <div className="notif">
        <img src={notifSimp} alt="notification" />
        <img src={notifActiveEllipse} alt="active notification" />
      </div>
      <div className="person">
        <img src={userImage} alt="user" />
        <span>App80</span>
        <FontAwesomeIcon icon={faAngleDown} />
      </div>
    </div>
  );
}

export default UserInfo;
