import React from "react";

function Status(props) {
  const styleCircle = {
    backgroundColor: props.color
  };
  const style = {
    color: props.color
  };
  return (
    <div className="status_single">
      <div className="circlePointer" style={styleCircle}></div>
      <span style={style}>{props.title}</span>
    </div>
  );
}

export default Status;
