// import React, { useState, useEffect } from "react";
import React from "react";
import { v4 as uuidv4 } from "uuid";
import Status from "../UtilityComponents/Status";
import SupplyDimensions from "./Products/PageUtilities/SupplyDimensions";
import SingleProdBtns from "./SingleProdBtns";

function SingleListImport(props) {
  const checkChangeHandler = e => {
    if (props.prodImpLi) {
      const elementID = props.prodImpLi.findIndex(el => el.id === props.singleLi.id);
      props.setProdImpLi(prev => {
        return [...prev.slice(0, elementID), { ...props.singleLi, checked: e.target.checked }, ...prev.slice(elementID + 1)];
      });
    } else if (props.MyProdLi) {
      const elementID = props.MyProdLi.findIndex(el => el.id === props.singleLi.id);
      props.setMyProdLi(prev => {
        return [...prev.slice(0, elementID), { ...props.singleLi, checked: e.target.checked }, ...prev.slice(elementID + 1)];
      });
    }
    // props.allCheckedHandler();
  };

  return (
    <div className="product-il__single">
      <div className="checkAndImage">
        <input type="checkbox" name="" id="" checked={props.singleLi.checked} onChange={checkChangeHandler} />
        <img src={`${process.env.PUBLIC_URL}/images/${props.singleLi.image}`} alt="" />
      </div>
      <div className="text">
        <div className="product">
          <p className="title">{props.singleLi.title}</p>
          <span className="catagory">{props.singleLi.catagory}</span>
          &nbsp;&nbsp;
          <span className="brandType">{props.singleLi.brandType}</span>
        </div>
        <div className="supplierAndStatus">
          <div className="supply">
            {/* {props.catagory === "myProd" && <SupplyDimensions title={props.singleLi.supplyDimensions.title} imgName={props.singleLi.supplyDimensions.imgName} imgAlt={props.singleLi.supplyDimensions.imgAlt} company={props.singleLi.supplyDimensions.company} />} */}

            {props.singleLi.supplyDimensions.map(supDim => (
              <SupplyDimensions key={uuidv4()} title={supDim.title} imgName={supDim.imgName} imgAlt={supDim.imgAlt} company={supDim.company} />
            ))}
          </div>
          <div className="status">
            {props.singleLi.statuses.map(st => (
              <Status key={uuidv4()} title={st.text} color={st.color} />
            ))}
          </div>
        </div>
      </div>
      <div className="costAndButton">
        <div className="cost">
          Cost:&nbsp;
          <span className="cost-actual">
            ${props.singleLi.cost.min} - ${props.singleLi.cost.max}
          </span>
        </div>
        <SingleProdBtns btnCat={props.catagory} />
      </div>
    </div>
  );
}

export default SingleListImport;
