import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAd } from "@fortawesome/free-solid-svg-icons";

function SingleTarget(props) {
  const lists = props.detTarget.map((li, index) => <li key={index}>{li}</li>);
  return (
    <div className="singleTarget">
      <div className="Ad-title">
        <FontAwesomeIcon className="singleTarget_AdIcon" icon={faAd} />
        &nbsp;&nbsp;
        <span>{props.title}</span>
      </div>
      <div className="details">
        <div className="details_way">
          <div className="details_single">
            <span>Location: </span>
            <span>{props.location}</span>
          </div>
          <div className="details_single">
            <span>Gender: </span>
            <span>{props.gender}</span>
          </div>
          <div className="details_single">
            <span>Age: </span>
            <span>{props.age}</span>
          </div>
          <div className="details_single">
            <span>Potential_reach: </span>
            <span>{props.pr}</span>
          </div>
        </div>
        <div className="details_way details_way_right">
          <span>Detailed Targeting:</span>
          <span className="second-span">INCLUDE PEOPLE WHO MATCH:</span>
          <ul>{lists}</ul>
        </div>
      </div>
    </div>
  );
}

export default SingleTarget;
