import React from "react";

function ShippingSingle(props) {
  return (
    <tr>
      <td>
        <input type="radio" name={props.name} defaultValue={props.label} />
        <label>{props.label}</label>
      </td>
      <td>{props.est}</td>
      <td>{props.shipCost}</td>
      <td>{props.available ? "Available" : "not Available"}</td>
    </tr>
  );
}

export default ShippingSingle;
