import React from "react";

function SingleAnnouncement(props) {
  return (
    <div className="announcements_single">
      <div className="announcements_head">
        <span className="announcements_date">{props.date}</span>
        <span
          className={`announcements_message ${
            props.value === "New Function" ? "new" : ""
          }`}
        >
          [{props.value}]
        </span>
        <span
          className={`announcements_status ${
            props.status === "HOT" ? "hot" : props.status === "NEW" ? "new" : ""
          }`}
        >
          {props.status}
        </span>
      </div>
      <p>{props.message}</p>
    </div>
  );
}

export default SingleAnnouncement;
