import React from "react";
import SourcingProductList from "./SourcingProductList";
import PageCounter from "../../PageCounter";
import SourceFilters from "./SourceFilters";
import SourcingDetails from "./SourcingDetails";

function SourceContentBody(props) {
  return (
    <div className={`source ${props.details ? "source-details" : ""}`}>
      <SourceFilters details={props.details} />
      {!props.details && (
        <div className="source_ads">
          <img src={`${process.env.PUBLIC_URL}/images/sourcingAds/products-AD-0911_cases.png`} alt="" />
          <img src={`${process.env.PUBLIC_URL}/images/sourcingAds/products-AD-0911_exercise.png`} alt="" />
          <img src={`${process.env.PUBLIC_URL}/images/sourcingAds/products-AD-0911_pet.png`} alt="" />
          <img src={`${process.env.PUBLIC_URL}/images/sourcingAds/products-AD-0911_sex.png`} alt="" />
          <img src={`${process.env.PUBLIC_URL}/images/sourcingAds/products-AD-0911_tech.png`} alt="" />
          <img src={`${process.env.PUBLIC_URL}/images/sourcingAds/products-AD-0911_winning.png`} alt="" />
        </div>
      )}
      {!props.details && <SourcingProductList sourceProductLi={props.sourceProductLi} />}
      {props.details && <SourcingDetails setModalName={props.setModalName} setShowModal={props.setShowModal} />}
      {!props.details && <PageCounter />}
    </div>
  );
}

export default SourceContentBody;
