import React from "react";
// import DropBtn from "../UtilityComponents/DropBtn";
import BackVector from "../../images/BackVector.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown } from "@fortawesome/free-solid-svg-icons";
import DropMenu from "../UtilityComponents/DropMenu";
import DropOption from "../UtilityComponents/DropOption";
import Alert from "../UtilityComponents/Alert";

function PageHead(props) {
  return (
    <>
      {!props.logState.loggedIn && <Alert setModalName={props.setModalName} setShowModal={props.setShowModal} className="notLoggedInAlert" type="warn" text="You don't have a platform account yet. When you have an account, you don't have to sign in by your store, you can sign in nextschain by any device easier with your account!" />}
      <div className="page-head">
        <div className="page-content__head">
          <h1>{props.title}</h1>

          {props.pathBase && (
            <p className="path">
              {props.pathBase}
              <span>{props.pathId}</span>
            </p>
          )}
        </div>
        {props.catagory === "editProd" && (
          <div className="headbuttons">
            <button className="BackVector">
              <img src={BackVector} alt="" />
              Go Back
            </button>
            {/* <DropBtn title="More Action"></DropBtn>
          <DropBtn title="Import to Store" /> */}
            <div className={`dbutton`}>
              <button>
                More Action
                <FontAwesomeIcon className={`dbutton_icon`} icon={faChevronDown} />
              </button>
              <DropMenu>
                <DropOption title="Save as draft" />
                <DropOption title="Split product" />
                <DropOption title="Remove product" />
              </DropMenu>
            </div>
            <div className={`dbutton`}>
              <button>
                Import to Store
                <FontAwesomeIcon className={`dbutton_icon`} icon={faChevronDown} />
              </button>
              <DropMenu>
                <DropOption title="Import to app8 account1" />
                <DropOption title="Import to account2" />
                <DropOption title="Import to all store" />
              </DropMenu>
            </div>
          </div>
        )}
        {props.billing && <button className="expOrder">Export order</button>}
      </div>
    </>
  );
}

export default PageHead;
PageHead.defaultProps = {
  catagory: ""
};
