import React from "react";
import { Link } from "react-router-dom";

function SideMenuSingle(props) {
  return (
    <div className="side-menu_single">
      <div className={`side-menu_head ${props.settingSidePage === props.to ? "activeSideMenuLink" : ""}`} onClick={() => props.setSettingSidePage(props.to)}>
        {/* <div className={`side-menu_head ${props.settingSidePage === props.to ? "activeSideMenuLink" : ""}`}> */}
        {/* <div className="side-menu_head"> */}
        <Link to={props.to}>
          <img src={`${process.env.PUBLIC_URL}/images/${props.imgName}`} alt="import List" className="normal" />
          <img src={`${process.env.PUBLIC_URL}/images/${props.imgName.slice(0, -4)}-active.png`} alt="import List" className="activeImg" />
          <h3>{props.title}</h3>
        </Link>
      </div>
    </div>
  );
}

export default SideMenuSingle;

SideMenuSingle.defaultProps = {
  ul: []
};
