import React from "react";
import SideMenu from "../SideMenu/SideMenu";
import Header from "../../Header/Header";
import Container from "../../UtilityComponents/Container";
import PricePageContent from "../Settings/Pricing/PricePageContent";
import TeamPageContent from "../Settings/Team/TeamPageContent";
import AccountPageContent from "./Account/AccountPageContent";
import Currency from "./Currency/Currency";
import Billing from "./Billing/Billing";
import Authorization from "./Authorization/Authorization";

function Settings(props) {
  return (
    <>
      <Header active="Settings" />
      <Container className="product-page sideBarPage">
        <SideMenu catagory="setting" settingSidePage={props.sidePage} setSettingSidePage={props.setSidePage} toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} />
        {props.account && <AccountPageContent accPage={true} setLogState={props.setLogState} logState={props.logState} setModalName={props.setModalName} setShowModal={props.setShowModal} toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} title={"MY ACCOUNT"} />}
        {props.pricing && <PricePageContent setModalName={props.setModalName} setShowModal={props.setShowModal} logState={props.logState} toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} />}
        {props.team && <TeamPageContent setModalName={props.setModalName} setShowModal={props.setShowModal} logState={props.logState} toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} title={"TEAM MANAGEMENT"} pathId="Unfulfilled" pathBase={`Orders > Merchant > `} />}
        {props.currency && <Currency setModalName={props.setModalName} setShowModal={props.setShowModal} logState={props.logState} toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} title={"Display Currency"} pathId="Unfulfilled" pathBase={`Orders > Merchant > `} />}
        {props.billing && <Billing setModalName={props.setModalName} setShowModal={props.setShowModal} logState={props.logState} toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} title={"Billing"} pathId="Unfulfilled" pathBase={`Orders > Merchant > `} />}
        {props.authorization && <Authorization setModalName={props.setModalName} setShowModal={props.setShowModal} logState={props.logState} toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} title={"STORE AUTHORIZATION"} pathId="Unfulfilled" pathBase={`Orders > Merchant > `} />}
      </Container>
    </>
  );
}

export default Settings;
