import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck, faExclamation } from "@fortawesome/free-solid-svg-icons";

function MainStatus(props) {
  return (
    <div className={`main_status ${props.type === "done" ? "done" : ""} ${props.type === "warn" ? "warn" : ""}`}>
      {/* // <div className={`main_status`}> */}
      <div className="iconDiv">
        {props.type === "done" && <FontAwesomeIcon className={`status-icon`} icon={faCheck} />}
        {props.type === "warn" && <FontAwesomeIcon className={`status-icon`} icon={faExclamation} />}
      </div>
      <p>
        There are
        <span>{props.highlight}</span>
        {props.endText}
        <button>GO DEAL</button>
      </p>
    </div>
  );
}

export default MainStatus;
