import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch, faCheck } from "@fortawesome/free-solid-svg-icons";
import DropBtn from "../../UtilityComponents/DropBtn";

function ProductHead(props) {
  const selectAllHandler = () => {
    props.setSelectAll(prev => !prev);
    props.setArrToSelect(prev =>
      prev.map(newLi => {
        if (props.selectAll) {
          return { ...newLi, checked: false };
        } else if (!props.selectAll) {
          return { ...newLi, checked: true };
        }
      })
    );
  };

  const listCount = () => {
    return props.arrToSelect.filter(fltr => fltr.filterCat === props.pathId).length;
  };

  if (props.catagory === "impProd") {
    return (
      <div className="product-il_head">
        <div className="product-il_search">
          <div className="input">
            <FontAwesomeIcon className={`input_icon_search`} icon={faSearch} />
            <input type="text" placeholder="Search title content..." />
          </div>
          <button>Search</button>
        </div>
        <div className="optionsFilter">
          <div className="selector">
            <div className="selector_iconDiv">
              {/* selectAll={props.selectAll} setSelectAll={props.setSelectAll} */}
              <div className={`selector_iconBox ${!props.selectAll ? "unSelect" : "Select"}`} onClick={selectAllHandler}>
                <FontAwesomeIcon className={`selector_icon`} icon={faCheck} />
              </div>
            </div>
            <span>Showing {listCount()} product</span>
          </div>
          <button>Bulk Edit</button>
          <button>Remove Product</button>

          <DropBtn title="Import to Store" dropHead="" />
        </div>
      </div>
    );
  } else if (props.catagory === "myProd") {
    return (
      <div className="product-il_head">
        <div className="product-il_search">
          <div className="input">
            <FontAwesomeIcon className={`input_icon_search`} icon={faSearch} />
            <input type="text" placeholder="Search title content..." />
          </div>
          <DropBtn title="All Store" className="inputTypeDrop" />
          <button>Search</button>
        </div>
        <div className="optionsFilter">
          <div className="selector">
            <div className="selector_iconDiv">
              <div className={`selector_iconBox ${!props.selectAll ? "unSelect" : "Select"}`} onClick={selectAllHandler}>
                <FontAwesomeIcon className={`selector_icon`} icon={faCheck} />
              </div>
            </div>
            <span>Showing {listCount()} product</span>
          </div>
          <button className="myProductBTN">Bulk Edit</button>
          <button className="myProductBTN">Remove Product</button>
        </div>
      </div>
    );
  } else {
    return (
      <div className="product-il_headrere">
        {/* <div className="product-il_search">
        <div className="input">
          <FontAwesomeIcon className={`input_icon_search`} icon={faSearch} />
          <input type="text" placeholder="Search title content..." />
        </div>
        <DropBtn title="All Store" className="inputTypeDrop" />
        <button>Search</button>
      </div>
      <div className="optionsFilter">
        <div className="selector">
          <div className="selector_iconDiv">
            <div className="selector_iconBox">
              <FontAwesomeIcon className={`selector_icon`} icon={faCheck} />
            </div>
          </div>
          <span>Showing 9 product</span>
        </div>
        <button className="myProductBTN">Bulk Edit</button>
        <button className="myProductBTN">Remove Product</button>
      </div> */}
      </div>
    );
  }
}

export default ProductHead;
