import React from "react";

function CardHead(props) {
  return (
    <div className="cHead">
      <h3>{props.type}</h3>
      <h2>
        {props.price}
        <span>{props.unit}</span>
      </h2>
      <button className={`${props.current ? "current" : ""}`}>{props.button}</button>
    </div>
  );
}

export default CardHead;
