import React, { useState } from "react";
import { v4 as uuidv4 } from "uuid";

import PageHead from "../../../Header/PageHead";
import SideMenuShow from "../../SideMenu/SideMenuShow";
import SingleCurrency from "./SingleCurrency";
// import SingleTeamMember from "./SingleTeamMember";

function Currency(props) {
  const [currencyList, setCurrencyList] = useState([
    { id: uuidv4(), storeName: "App80 Store-1" },
    { id: uuidv4(), storeName: "App80 Store-1" },
    { id: uuidv4(), storeName: "App80 Store-1" },
    { id: uuidv4(), storeName: "App80 Store-1" }
  ]);

  return (
    <div className="page-content currency-page">
      <SideMenuShow toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} />
      <PageHead setModalName={props.setModalName} setShowModal={props.setShowModal} logState={props.logState} title={props.title} pathBase={props.pathBase} pathId={props.pathId} />
      <div className="page-content__body">
        <p className="currPara">All products price will be displayed in the currency you choose. </p>
        <form action="" className="currencies-list">
          {currencyList.map(store => (
            <SingleCurrency setCurrencyList={setCurrencyList} key={store.id} id={store.id} storeName={store.storeName} />
          ))}
        </form>
      </div>
    </div>
  );
}

export default Currency;
