import React from "react";
import PageHead from "../../../Header/PageHead";
import Alert from "../../../UtilityComponents/Alert";
import AccDet from "../../AccDet";
import SideMenuShow from "../../SideMenu/SideMenuShow";
import AccountInfo from "./AccountInfo";
import ContactInfo from "./ContactInfo";

function AccountPageContent(props) {
  return (
    // <div className="page-content">
    //   <PageHead title={props.title} />

    //   <div className="page-content__body">
    //     {/* {props.catagory === "main" && <ProductImportProducts toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} />}
    //     {props.catagory === "impProd" && <ProductList catagory={props.catagory} />}
    //     {props.catagory === "myProd" && <ProductList catagory={props.catagory} />} */}
    //   </div>
    // </div>

    <div className="page-content team-page account-page">
      <SideMenuShow toggleSide={props.toggleSide} setToggleSide={props.setToggleSide} />
      <PageHead setModalName={props.setModalName} setShowModal={props.setShowModal} logState={props.logState} title={props.title} pathBase={props.pathBase} pathId={props.pathId} />
      <div className="page-content__body">
        <p className="pageDesc">Account Information</p>
        <div className="team-page-body">
          <AccDet accPage={props.accPage} setModalName={props.setModalName} setShowModal={props.setShowModal} logState={props.logState} />
          {props.logState.loggedIn && props.logState.userPlan === "free" && <Alert type="warn" text="Your current plan is Free, Wi wont charge your account is pro plan currently, you will be charged $59.98 monthly!" />}
          {props.logState.loggedIn && (
            <>
              <AccountInfo setModalName={props.setModalName} setShowModal={props.setShowModal} />
              <hr />
              <ContactInfo />
            </>
          )}
          {!props.logState.loggedIn && (
            <>
              <AccountInfo setModalName={props.setModalName} setShowModal={props.setShowModal} notLoggedIn={true} />
            </>
          )}
        </div>
      </div>
      <button
        onClick={() =>
          props.setLogState(prev => {
            return { ...prev, loggedIn: true };
          })
        }
      >
        Dummy button for logging in
      </button>
      <button
        style={{ margin: "0 1rem" }}
        onClick={() =>
          props.setLogState(prev => {
            return { ...prev, loggedIn: false };
          })
        }
      >
        Dummy button for logging out
      </button>
    </div>
  );
}

export default AccountPageContent;
